.loader {
  width: 48px;
  height: 48px;
  color: #d1cdcd;
  box-sizing: border-box;
  margin: 40px auto;
  animation: rotation 1s linear infinite;
  display: block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  background-color: #d1cdcd;
  border-radius: 50%;
  animation: scale50 1s ease-in-out infinite;
  position: absolute;
  top: 0;
}

.loader:before {
  background-color: #594e4e;
  animation-delay: .5s;
  top: auto;
  bottom: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes scale50 {
  0%, 100% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }
}

